.navbar.docs-container {
  // display: flex;
  display: none;
  justify-content: space-between;
  height: 80px;
  padding: 0 60px;
  border-bottom: 1px solid #ececec;
  background-color: #fff;

  .logo {
    margin: 0;
    line-height: 80px;
    color: #51a7e8;
  }

  .nav {
    .item {
      padding-left: 2em;
      line-height: 80px;
      &:hover {
        color: #51a7e8;
      }
    }
  }
}

